// REACT COMPONENT STYLING

// HEADER MENU -----------------------------------------
.menu-item {
    font-family: var(--base-font-family) !important;
}

// CATEGORY FILTER ---------------------------------------
#category-container {
    width: 80%;
    margin: 0.5em auto;

    display: flex;
    align-items: center;
    justify-content: space-between;

    .category-button {
        background-color: #bdbdbd;
        border-style: none;
        border-radius: 0.2em;
        padding: 0.5em;
        width: 100%;
        margin: 0.1em;
        text-wrap: nowrap;

        font-family: var(--base-font-family)
    }
    .category-button:hover {
        background-color: #FFC600;
        cursor: pointer;
        font-weight: bold;
    }
    .category-button.active {
        background-color: #FFC600;
        cursor: pointer;
        font-weight: bold; 
    }
    .category-list {
        width: 100%;
        display: flex;
        .MuiInputBase-root {
            background-color: #2b2b2b;
            width: 40%;
            margin: auto;
        }
        .Mui-focused .MuiOutlinedInput-notchedOutline {
            border: 2px solid #FFC600 !important;
        }
    }
}

// TAG FILTER --------------------------------------
#tag-container {
    margin: auto;
    width: 80%;
    padding: 0.25em;
    margin-bottom: 1em;

    border-radius: 0.2em;
    background-color: #2b2b2b;

    .tag-button {
        margin: 0.25em;
        padding: 0.25em 0.5em;
        border-radius: 0.2em;
        background-color: #000000;
        border: 1px solid #000000;
        color: #bdbdbd;
        font-size: 0.75em;

        .material-icons {
            font-size: 0.75em;
            align-items: center;
        }
    }
    .tag-button:hover, .tag-button.active {
        border: 1px solid #FFC600;
        cursor: pointer;
        color: #FFC600;
    }
    .message {
        font-family: var(--base-font-family);
        font-size: 0.5em;
        color: #FFC600;
        text-align: center;

        margin: auto;
        padding: 1em;
    }
}

// SKELETON ------------
.skel-grid{
    height: 100%;
    display: flex;
    flex-direction: row;

    .skel-box {
        padding-left: 1em;
    }
}    

// EVENT CARD --------------------------------------
.card-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 2fr));
}

.card-grid .MuiGrid-item {
    display: flex;
    flex-direction: column;
}
.card {
    background-color: #2b2b2b !important;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .event-content {
        padding: 0;
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        position: relative;
    }

    .event-image {
        display: flex;
        height: 12em;
    }

    .event-stall-box {
        background-color: #000000;
        text-align: center;
        border-radius: 50%;
        width: 2em;
        height: 2em;
        padding: 0.2em;
        position: absolute;
        top: 0;
        right: 0;
        margin: 0.5em;

        p {
            color: #FFC600;
        }
        .stall {
            font-size: 0.5em;
            margin: 0;
            padding: 0;
            transform: translateY(2px);
        }
        .event-stall-number {
            margin: 0;
            transform: translateY(-3px);
        }
    }

    .event-title-box {
        display: flex;
        align-items: center;
        justify-content: space-between;
        
        // event title
        .event-title {
            font-family: var(--base-font-family);
            font-weight: bold;
            font-size: 100%;
            color: #FFFFFF;
            padding: 1em;
        }
    }

    .event-description {
        font-family: var(--base-font-family);
        color: #FFFFFF;
        font-size: 75%;
        padding: 0 1.5em;
        padding-bottom: inherit;
        margin-bottom: revert;
    }

    .event-times {
        font-family: var(--base-font-family);
        color: #FFFFFF;
        font-size: 75%;
        padding: 0 1.5em 1em;
        margin-top: auto;
        position: absolute;
        bottom: 0;
        right: 0;
        text-align: right;
    }

    .event-button {
        display: flex;
        justify-content: center;
        padding: 0 1em;
        border: none;
        z-index: 10;
        pointer-events: all;

        .material-icons {
            color: #bdbdbd;
            width: 24px;
        }
    }
    .event-button:hover, .event-button.active {
        .material-icons {
            cursor: pointer;
        }
    }
    .hidden {
        display: none;
    }
}

// VENUE NAVIGATION ----------------------------------------
.venue-nav {
    justify-content: center;

    .MuiButtonBase-root {
        background-color: #bdbdbd;
        border-radius: 0.2em;
        border: 1px solid #bdbdbd;
        color: #000000;
        text-transform: capitalize;
        padding: 0.75em;
        margin: 0.25em;
    }
    .MuiButtonBase-root:hover {
        background-color: #FFC600;
        font-weight: bold;
        border: 1px solid #FFC600;
    }
}
.venue-nav-list {
    width: 100%;
    display: flex;
    .MuiInputBase-root {
        background-color: #2b2b2b;
        width: 40%;
        margin: auto;
    }
    .Mui-focused .MuiOutlinedInput-notchedOutline {
        border: 2px solid #FFC600 !important;
    }
}

// ABOUT - CONTACT FORM -----------------------------------------
.form-container {
    height: auto;
    background-color: #2b2b2b;
    border-radius: 0.2em;
    padding: 0.5em 1em;
    font-family: var(--base-font-family);

    .form-title {
        font-size: 1.5em;
        text-align: center;
        font-weight: bold;
    }
    .form-section {
        background-color: #505050;
        padding: 1em;
        margin: 0.5em 0;
        width: auto;
        border-radius: 0.2em;

        .form-sub {
            font-size: 0.9em;
            margin-bottom: 0.5em;
        }
        .form-text, .form-enquiry {
            margin: 0.25em 0;
            border-radius: 0.2em;
            font-family: var(--base-font-family);
            // text-field background
            .MuiInputBase-root {
                background-color: #ffffff;
                margin: 0.1em;
            }
            // text-field label colour
            .MuiInputBase-root, label {
                color: #000000;
                font-size: 0.75em;
            }
            // text-field outline not focused
            .MuiOutlinedInput.root .MuiOutlinedInput-notchedOutline {
                border: 1px solid #000000;
            }
            // text-field outline when focused
            .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline {
                border: 0.2em solid #FFC600;
            }
            // text-field label shrink colour
            .MuiInputLabel-shrink {
                color: #FFC600;
            }
        }
        .form-checkbox {
            .MuiCheckbox-root {
                color: #bdbdbd;
            }
            .MuiCheckbox-root.Mui-checked  {
                color: #FFC600;
            }
            .MuiFormControlLabel-label {
                font-family: var(--base-font-family);
                color: #FFFFFF;
                font-size: 0.75em;
            }
            .MuiButtonBase-root.MuiCheckbox-root {
                padding: 0.3em;
                margin-left: 0.3em;
            }
        }
        .form-enquiry {
            width: 100%;
        }
    }
    .form-controls {
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;

        .form-button {
            background-color: #FFC600;
            color: #000000;
            padding: 0.5em 1em;
        }
    }
    .MuiSnackbar-root {
        left: 43%;
    }
}

