// ADMIN NAVIGATION ----------------------------------
#admin-icon {
    font-size: 5em;
    margin: 0.2em auto;
    color: #FFC600;
}
.admin-text {
    color: #FFC600;
    font-family: var(--base-font-family) !important;
    text-align: center;
}
.admin-text:first-of-type {
    font-size: 1.5em;
    font-weight: bold !important;
}

// NEW EVENT FORM -------------------------------------------------
.new-event-form {
    p {
        color: #000000;
    }
    .form-content {
        margin-top: 0.5em;
    }
    // text fields
    .MuiInputBase-root.MuiOutlinedInput-root {
        font-family: var(--base-font-family);
        font-size: 0.75em !important;
    }
    .form-text {
        margin: 0.25em 0;

        .MuiInputBase-root, label { // textfield colour
            color: #000000;
            font-size: 0.75em;
        }
        .MuiOutlinedInput-notchedOutline {
            border: 0.1em solid #000000;
        }
        .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline {
            border: 0.2em solid #FFC600;
        }    
        .MuiInputLabel-shrink {
            color: #FFC600;
        }
    }

    // vendor details
    .vendor-details {
        display: flex;
        justify-content: space-between;
        .form-text {
            width: 32.5%;
        }
    }

    // upload image
    .image-input {
        margin: 1em 0 0.5em 0;
        .upload-image {
            background-color: #FFC600;
            height: 2.5em;
            width: 100%;
            margin-left: 1em;
        }
        .MuiButtonBase-root.MuiButton-root {
            color: #000000;
            border: none;
        }
    }

    // dropdown
    .category-dropdown {
        .MuiInputBase-input {
            padding: 8.5px 14px;
        }
        .MuiOutlinedInput-notchedOutline {
            border: 1px solid #000000;
        }
        
    }

    // checkboxes
    .form-section { //box containing tags
        background-color: #f5f5f5;
        padding: 1em;
        margin: 0.5em 0;
        border-radius: 0.2em;
    }
    .tag-grid { // turn display to grid and adjust layout
        display: grid;
        grid-template-columns: repeat(3, 33% [col-start]);
    }
    #tag-section { // checkbox display
        .form-sub {
            font-size: 1em;
        }
        .MuiTypography-root.MuiTypography-body1.MuiFormControlLabel-label { // checkbox text
            font-size: 0.75em;
        }
        .form-checkbox { // checkbox colors, checked and unchecked
            .MuiCheckbox-root {
                color: #505050;
            }
            .MuiCheckbox-root.Mui-checked {
                color: #505050;
            }
            .MuiButtonBase-root.MuiCheckbox-root {
                padding: 0.3em;
                margin-left: 0.3em;
            }
        }
    }

    // time details
    .time-details {
        display: flex;
        justify-content: space-between;

        .time-col {
            width: 49.5%;
        }
    }

    .event-button {
        border: 0.1em solid #000000 !important;
        background-color: #FFC600;
        color: #000000;
        box-shadow: none;
        margin-top: 1em;
    }
    .MuiButtonBase-root.MuiButton-root:hover {
        background-color: #000000;
        color: #FFFFFF;
    }
}

// MANAGE EVENTS ------------------------------------------------
.manage-event-container {
    width: 80%;
    height: 25em;
    background-color: #2b2b2b;
    margin: auto;
    padding: 1em;
}

// RESPONSIVE - TABLET ---------------------------------
@media all and (max-width: 768px) {
    .event-card {
        max-width: 32%;
    }
    .form-container {
        width: auto;
    }
    .MuiList-root.MuiMenu-list {
        background-color: #000000;
        font-family: var(--base-font-family);
        font-size: 1em;
        padding: 1em;
        border: 1px solid #FFC600;
        border-radius: 0.2em;
        width: 40vw;
    }
    .venue-nav {
        width: 100%;

        .MuiBox-root {
            display: flex;
            flex-direction: row;
            justify-content: center;
            margin-bottom: 1em;
            flex-wrap: wrap;
        }
    }
    .venue-nav-list {
        display: flex;
        justify-content: center;
    }
}

@media all and (max-width: 600px) {
    #category-container {
        width: 100%;
    }
}

// RESPONSIVE - MOBILE --------------------------------------
@media all and (max-width: 500px) {
    .event-card {
        max-width: 20em !important;
        margin: 0 0.5em;
    } 
    .card-grid {
        display: flex;
        overflow-x: auto;
        flex-wrap: nowrap !important;
    }
    .card-grid .MuiGrid-item {
        flex: 0 0 auto;
        width: 18em;
    }
}